.App {
  text-align: center;
  height: 95%;
  overflow: hidden; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #9bbeec;
  height: 73px;
  width: 100%;
  padding: 20px;
  color: #070606;
  background-image: url(/ReactDemo/static/media/small_logo_tr.84f49632.png);
  background-repeat: no-repeat;
  background-size: 100% 100%; }

.App-footer {
  border-top: 1px gray solid;
  height: 100%;
  max-height: 80px;
  margin-top: 2px;
  background-color: #8b8a87; }

.App-title {
  font-size: 2em;
  text-align: center;
  font-weight: 600; }

.app-menubar {
  margin-top: 5px;
  background-color: brown;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content; }

.dropdown-menu {
  background-color: red; }
  .dropdown-menu a {
    color: black; }
  .dropdown-menu a:hover {
    background-color: blue; }

.app-menubar1 .menu-icon {
  display: none;
  text-align: left;
  margin: 10px;
  padding: 2px;
  width: 35px;
  border: 3px gray solid;
  background-color: transparent;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat; }

.App .App-Is-Offline {
  display: block;
  background-color: red;
  position: fixed;
  bottom: 10px;
  height: 35px;
  width: 100%;
  z-index: 1000;
  padding: 5px; }

.App .App-Is-Online {
  display: none; }

.pages-container {
  height: calc(100vh - 180px);
  width: 100%;
  overflow-y: auto;
  display: flex;
  justify-content: center;
  padding-top: 5px;
  overflow-x: hidden; }

@media only screen and (min-width: 450px) {
  .App-header {
    background-image: url(/ReactDemo/static/media/logo_tr.661be74e.png);
    height: 180px; }
  .App-title {
    font-size: 3.5em;
    padding-top: 40px; }
  .pages-container {
    height: calc(100vh - 180px); } }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.page-viewer-container {
  height: 90%;
  width: 100%; }

.time-container {
  float: right;
  padding-right: 25px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif; }

.app-time {
  float: right;
  width: 100%; }

.contact-us-form {
  width: 400px; }
  .contact-us-form label {
    text-align: left; }
  .contact-us-form .desc-top {
    margin: 10px; }

.page-container {
  width: calc(100vw - 10px) !important;
  overflow: hidden;
  border: 1px;
  background-color: #e9dcdc;
  border-radius: 5px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding-left: 20px;
  padding-right: 20px; }
  .page-container .ss-row {
    width: 100%;
    float: left;
    padding: 10px; }
    .page-container .ss-row .ss-col-4 {
      width: 400px;
      margin: 10px;
      float: left; }

.page-container .page-item {
  padding: 2px;
  border-radius: 5px;
  margin: 15px;
  background-color: white;
  /* rgb(104, 85, 129);*/
  box-shadow: 0 1px 3px black;
  width: calc(100vw - 50px);
  position: relative; }

@media only screen and (min-width: 450px) {
  .page-container .page-item {
    width: calc( 100vw / 2 - 45px); } }

@media only screen and (min-width: 800px) {
  .page-container .page-item {
    width: calc( 100vw / 3 - 45px); } }

.page-container .page-item .new-badge-hidden {
  display: none; }

.page-container .page-item .new-badge {
  border-bottom: 50px transparent solid;
  border-right: 50px #07ff1b solid;
  /*float: right;*/
  width: 60px;
  height: 40px;
  position: absolute;
  top: 0px;
  right: 0px; }

.page-container .page-item .new-badge .rotate-span {
  width: 50px;
  height: 20px;
  text-align: center;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  float: right;
  position: relative;
  top: 9px;
  right: -58px;
  font-size: 14px;
  font-weight: 600; }

.page-container .page-item .page-item-header {
  margin-bottom: 10px;
  height: 70px;
  border-bottom: 2px gray solid;
  display: flex; }

.page-container .page-item .page-item-header .page-item-title {
  font-weight: 600;
  font-size: 18px;
  width: 100%;
  text-align: center; }
  .page-container .page-item .page-item-header .page-item-title a {
    top: 20px;
    position: relative; }

.page-container .page-item .page-item-header .page-item-img {
  width: 50px;
  margin: 10px; }

.page-container .page-item .img-not-found {
  display: none; }

.page-container .page-item-info {
  text-align: justify;
  padding: 10px;
  font-family: nazanin, tahoma, "Times New Roman", Times, serif;
  font-size: 16px;
  color: black;
  line-height: 28px; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

